body {

  margin: 0;
  padding: 0;
  background-color: black;
  color: white;

}



.main-container {

  flex-direction: column;
  margin-left: 10px;

}




.movielistcontainer {

  display: flex;
  overflow: auto;

}

.header {

  display: flex;
  color: white;
  justify-content: space-between;
  align-items: center;
  padding: 10px;

}

.myinput {
  width: 120px;
  height: 35px;
  border-radius: 10px;

}


.movielist {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 5px;



}


.posters {

  width: 170px;
  height: 220px;
  border-radius: 10px;


}

.mybutton {
  width: 120px;
  height: 30px;
  border-radius: 8px;
  background-color: rgb(206, 32, 61);
  color: aqua;
  margin-top: 10px;
  margin-bottom: 3px;
  margin-left: 25px;
  margin-right: 5px;
}